import React, { FunctionComponent, useEffect, useState } from "react";
import "./welcome.scss";
// import { useNavigate } from "react-router-dom";
import Input from "../../components/Input";
import Select from "../../components/Select";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const Welcome: FunctionComponent = () => {
  const navigate = useNavigate();
  interface Student {
    idStudent: number;
    studentId: string;
    studentFirstName?: string;
  }

  const headingTexts = [
    "Protge ton compte !",
    "Choisis tes options",
    "On tappelle comment ?",
    "On valide ?",
  ];
  
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [nameStudent, setNameStudent] = useState("");
  const [usernameArray, setUsernameArray] = useState<Student[]>([]);
  const [screen, setScreen] = useState("default");
  const [error, setError] = useState([false, ""]);
  const [slideSignup, setslideSignupSignup] = useState(1);
  const [specialtySelectValue, setSpecialtySelectValue] = useState("default");
  const [innovationSelectValue, setInnovationSelectValue] = useState("default");
  const [expressionSelectValue, setExpressionSelectValue] = useState("default");
  const [inputFirstName, setInputFirstName] = useState("");
  const [inputLastName, setInputLastName] = useState("");
  const [profilePicture,setProfilePicture] = useState("");
  const [previewProfilePicture, setPreviewProfilePicture] = useState(
    "/default-avatar.png"
  );

  const [optionsUser, setOptionsUser]: any[] = useState([]);

  useEffect(() => {
    
    if (screen === "default") {
      const loadStudentIds = async () => {
        const response = await axios.get(
          "http://192.168.0.107/pwa/api/routes/api.php?action=getAllUsernames"
        );
        setUsernameArray(response.data);
      };
      loadStudentIds();
    } else if (screen === "signin" && slideSignup === 1) {
      const loadOptions = async () => {
        const response = await axios.get(
          "http://192.168.0.107/pwa/api/routes/api.php?action=getAllOptions"
        );
        setOptionsUser(response.data);
        
      };

      loadOptions();
    }
  }, [screen,slideSignup]);

  const clickNextSlide = async (e: any) => {
    switch (slideSignup) {
      case 1:
        if (password === confirmPassword) {
          setslideSignupSignup(slideSignup + 1);
        } else {
          setError([true, "Mot de passes diffrents"]);
          setTimeout(() => {
            setError([false, ""]);
          }, 1000);
        }
        break;
      case 2:
        setslideSignupSignup(slideSignup + 1);
        break;
      case 3:
        if (inputFirstName !== "" && inputLastName !== "") {
          setslideSignupSignup(slideSignup + 1);
        }
        break;
      case 4:
        
        
      e.preventDefault();
        try {
          const formData = new FormData();

          // Ajouter les champs de votre formulaire  l'objet FormData
          formData.append('username', username);
          formData.append('password', password);
          formData.append('specialty', specialtySelectValue);
          formData.append('innovationCursus', innovationSelectValue);
          formData.append('expressionCursus', expressionSelectValue);
          formData.append('firstName', inputFirstName);
          formData.append('lastName', inputLastName);
          formData.append('profilePicture', profilePicture);

          const response = await axios.post(
            "http://192.168.0.107/pwa/api/routes/api.php?action=signup",
            formData,{
              headers: {
                'content-type': 'multipart/form-data'
              }
            }

          );

          if (response.status === 200) {
            
            if (response.data.valid){
              localStorage.setItem('id', response.data.message.idStudent);
              localStorage.setItem('studentId', response.data.message.studentId);
              localStorage.setItem('firstName', response.data.message.firstName);
              localStorage.setItem('lastName', response.data.message.lastName);
              localStorage.setItem('profilePicturePath', response.data.message.profilePicture);

              navigate("/home", { replace: true });
            }
          }
        } catch (error) {
          console.log(error);
        }

        break;

      default:
        break;
    }
  };

  const handleValidUsername = (e: any) => {
    const student = usernameArray.find(
      (student) => student.studentId === username
    );

    setNameStudent(student?.studentFirstName!);
    student ? setScreen("login") : setScreen("signin");
  };

  const handleLoginButton = async (e: any) => {
    e.preventDefault();
    if (username && password) {
      try {
        const formData = new FormData();
        formData.append('user', username);
        formData.append('input', password);

        const response = await axios.post(
          "http://192.168.0.107/pwa/api/routes/api.php?action=verifyLogin",
          formData,{
              headers: {
                'content-type': 'multipart/form-data'
              }

        });

        if (response.data.valid) {
          localStorage.setItem("id", response.data.message.idStudent);
          localStorage.setItem("studentId", response.data.message.studentId);
          localStorage.setItem(
            "firstName",
            response.data.message.studentFirstName
          );
          localStorage.setItem(
            "lastName",
            response.data.message.studentLastName
          );
          localStorage.setItem(
            "profilePicturePath",
            response.data.message.profilePicturePath
          );

          navigate("/home", { replace: true });
        } else {
          setError([true, response.data.message]);
          setTimeout(() => {
            setError([false, ""]);
          }, 1000);
        }
      } catch (error) {
        console.log(error);
      }
    }
  };

  const handleChangeUsername = (e: any) => {
    const value = e.target.value;
    setUsername(value);
  };

  const handleChangePassword = (e: any) => {
    const value = e.target.value;
    setPassword(value);
  };

  const handleChangeConfirmPassword = (e: any) => {
    const value = e.target.value;
    setConfirmPassword(value);
  };

  const handleSpecialtySelectChange = (e: any) => {
    const value = e.target.value;
    setSpecialtySelectValue(value);
  };
  const handleInnovationSelectChange = (e: any) => {
    const value = e.target.value;
    setInnovationSelectValue(value);
    // updateSubmitButtonColor(specialtySelectValue, value, expressionSelectValue);
  };
  const handleExpressionSelectChange = (e: any) => {
    const value = e.target.value;
    setExpressionSelectValue(value);
    // updateSubmitButtonColor(specialtySelectValue, innovationSelectValue, value);
  };

  const handleFirstNameInput = (e: any) => {
    const value = e.target.value;
    setInputFirstName(value);
  };

  const handleLastNameInput = (e: any) => {
    const value = e.target.value;
    setInputLastName(value);
  };

  const handleFile = (e:any) => {
    const image = e.target.files[0];
    setPreviewProfilePicture(URL.createObjectURL(image));
    setProfilePicture(image);
  }

  return (
    <div className="wrapper welcome">
      {screen === "default" && (
        <section className="username">
          <h1 className="message">Quel est ton identifiant ?</h1>
          <Input
            id={"email"}
            value={username}
            placeholder="Entre ton identifiant"
            name={"username"}
            onChange={handleChangeUsername}
          />
          {username !== "" && (
            <div className="ctaButton enabled" onClick={handleValidUsername}>
              <p>Continuer</p>
            </div>
          )}
        </section>
      )}

      {screen === "login" && (
        <section className="password">
          <h1 className="message">
            Ravi de te revoir, <span className="name">{nameStudent}</span> !
          </h1>
          <Input
            id={"password"}
            value={password}
            placeholder="Entre ton mot e passe"
            name={"password"}
            type={"password"}
            onChange={handleChangePassword}
          />
          <footer className="navigation">
            <div
              className="navigation__previous"
              onClick={(e) => {
                e.preventDefault();
                setScreen("default");
                setPassword("");
              }}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                fill="none"
              >
                <path
                  d="M12 19L5 12M5 12L12 5M5 12H19"
                  stroke="#09090B"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
            </div>

            <div
              className={
                "navigation__next ctaButton " +
                (password === "" ? "disabled" : "enabled")
              }
              onClick={handleLoginButton}
            >
              <p>Connexion</p>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
              >
                <path
                  d="M5 12H19M19 12L12 5M19 12L12 19"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
            </div>
          </footer>
        </section>
      )}

      {screen === "signin" && (
        <section className="password">
          <div className="indicators">
            {[1, 2, 3, 4].map((slide) => (
              <div
                key={slide}
                className={`indicators__pill slide ${
                  slide <= slideSignup ? "active" : ""
                }`}
              ></div>
            ))}
          </div>
          <h1 className="message">{headingTexts[slideSignup - 1]}</h1>

          {slideSignup === 1 && (
            <section className="inputs">
              <Input
                id={"password"}
                value={password}
                placeholder="Entre ton mot de passe"
                name={"password"}
                type="password"
                onChange={handleChangePassword}
              />
              <Input
                id={"confirmPassword"}
                value={confirmPassword}
                placeholder="Confirme ton mot de passe"
                type="password"
                name={"confirmPassword"}
                onChange={handleChangeConfirmPassword}
              />
            </section>
          )}

          {slideSignup === 2 && (
            <section className="inputs">
              <Select
                id="specialty"
                value={specialtySelectValue}
                label="Slectionne ta spcialit"
                name="specialty"
                onChange={handleSpecialtySelectChange}
                options={optionsUser.specialties.map(
                  (specialty: { specialtyName: any }) => specialty.specialtyName
                )}
              />

              <Select
                id="innovation"
                value={innovationSelectValue}
                label="Slectionne ton parcours d'innovation"
                name="innovation"
                onChange={handleInnovationSelectChange}
                options={optionsUser.innovations.map(
                  (innovation: { innovationCursusName: any }) =>
                    innovation.innovationCursusName
                )}
              />

              <Select
                id="expression"
                value={expressionSelectValue}
                label="Slectionne ton parcours d'expression"
                name="expression"
                onChange={handleExpressionSelectChange}
                options={optionsUser.expressions.map(
                  (expression: { expressionCursusName: any }) =>
                    expression.expressionCursusName
                )}
              />
            </section>
          )}

          {slideSignup === 3 && (
            <section className="inputs">
              <Input
                id="firstName"
                value={inputFirstName}
                type="text"
                name="firstName"
                onChange={handleFirstNameInput}
                placeholder="Marc"
              />

              <Input
                id="lastName"
                value={inputLastName}
                type="text"
                name="lastName"
                onChange={handleLastNameInput}
                placeholder="Dupont"
              />
            </section>
          )}

          {slideSignup === 4 && (
            <section className="inputs">
              <div className="profile">
                <input
                  type="file"
                  name="profilePicture"
                  id="profilePicture"
                  accept="image/*"
                  placeholder="Choisis ta photo de profil"
                  onChange={handleFile}
                />

                <div className="profilePicture">
                  <label htmlFor="profilePicture">
                    <img src={previewProfilePicture} alt="" />
                    <div className="profilePicture__edit">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 16 16"
                        fill="none"
                      >
                        <path
                          d="M4.53452 10.869L11.0394 4.36409L10.1325 3.45718L3.62761 9.96205V10.869H4.53452ZM5.06622 12.1517H2.34485V9.43035L9.67902 2.09617C9.7993 1.97593 9.96241 1.90839 10.1325 1.90839C10.3025 1.90839 10.4657 1.97593 10.5859 2.09617L12.4004 3.91064C12.5206 4.03091 12.5882 4.19402 12.5882 4.36409C12.5882 4.53416 12.5206 4.69727 12.4004 4.81755L5.06622 12.1517V12.1517ZM2.34485 13.4345H13.8897V14.7172H2.34485V13.4345Z"
                          fill="currentColor"
                        />
                      </svg>
                    </div>
                  </label>

                  {previewProfilePicture !== "/default-avatar.png" && (
                    <div
                      className="profilePicture__delete"
                      onClick={() =>
                        setPreviewProfilePicture("/default-avatar.png")
                      }
                    >
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 20 22"
                        fill="none"
                      >
                        <path
                          d="M1 5H19M17 5V19C17 20 16 21 15 21H5C4 21 3 20 3 19V5M6 5V3C6 2 7 1 8 1H12C13 1 14 2 14 3V5"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        />
                      </svg>
                    </div>
                  )}
                </div>
                
                <div className="profileTexts">
                <h2 className="profile__name">
                  {inputFirstName + " " + inputLastName}
                </h2>
                <h4 className="profile__id">@{username}</h4>
                </div>
               
              </div>

              <div className="options">
                <div className="options__item">
                  <h1>Spcialit</h1>
                  <h2>{specialtySelectValue}</h2>
                </div>
                <div className="options__item">
                <h1>Parcours d'innovation</h1>
                <h2>{innovationSelectValue}</h2>
                </div>
                <div className="options__item">
                <h1>Parcours d'expression</h1>
                <h2>{expressionSelectValue}</h2>
                </div>
              </div>
             
            </section>
          )}

          <footer className="navigation">
            <div
              className="navigation__previous"
              onClick={(e) => {
                e.preventDefault();

                if (slideSignup === 1) {
                  setScreen("default");
                  setPassword("");
                  setConfirmPassword("");
                  setInputLastName("");
                  setInputFirstName("");
                  setExpressionSelectValue("default");
                  setSpecialtySelectValue("default");
                  setInnovationSelectValue("default");
                } else {
                  setslideSignupSignup(slideSignup - 1);
                }
              }}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                fill="none"
              >
                <path
                  d="M12 19L5 12M5 12L12 5M5 12H19"
                  stroke="#09090B"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
            </div>

            { 
              ((slideSignup === 1 && password === confirmPassword && password !== "") || 
              (slideSignup === 2 && specialtySelectValue !== "default" && innovationSelectValue !== "default" && expressionSelectValue !=="default" ) ||
              (slideSignup === 3 && inputFirstName !== "" && inputLastName !== "") ||
              (slideSignup === 4))  && (<div
              className={"navigation__next ctaButton enabled"}
              onClick={clickNextSlide}
            >
              {slideSignup === 4 ? (<p>Commencer</p>) : (<p>Suivant</p>)}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
              >
                <path
                  d="M5 12H19M19 12L12 5M19 12L12 19"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
            </div>)}
          </footer>
        </section>
      )}
      {error[0] && <p className="popup">{error[1]}</p>}
    </div>
  );
};

export default Welcome;
