import React, {
  FunctionComponent,
  ReactNode,
  useEffect,
  useRef,
  useState,
} from "react";
import "./Popup.scss"; // Assurez-vous d'avoir un fichier CSS pour le style de la popup

interface PopupProps {
  children: ReactNode;
  showPopup: boolean;
  showValidPopup?:boolean;
  title:string;
  setShowPopup: (newValue: boolean) => void;
  setShowValidPopup?: (newValue: boolean) => void;
}
const Popup: FunctionComponent<PopupProps> = ({
  children,
  showPopup,
  setShowPopup,
  title,
  showValidPopup,
  setShowValidPopup
}) => {
  const [animation, setAnimation] = useState("open");
  const [startY, setStartY] = useState(0); // Position verticale du touchStart
  const [deltaY, setDeltaY] = useState(0); // Diffrence de position verticale entre touchStart et touchMove
  const overlayRef = useRef<HTMLDivElement>(null); // Ref pour la popup
  const popupRef = useRef<HTMLDivElement>(null); // Ref pour la popup
  const [heightPopup,setHeightPopup] = useState(0);

  useEffect(()=>{
    setHeightPopup(popupRef.current?.clientHeight!);
  },[])
  const handleTouchStart = (e: any) => {
    setStartY(e.touches[0].clientY); // Enregistrer la position verticale du touchStart
  };

  const handleTouchMove = (e: any) => {
    const currentY = e.touches[0].clientY;
    const newDeltaY = currentY - startY; // Calculer la diffrence de position verticale
    console.log(1-newDeltaY/popupRef.current?.clientHeight!);
    if (!popupRef.current?.querySelector('.contentPopup')!.contains(e.target)) {

            if (newDeltaY >= 0) {
                popupRef.current!.style.transition = ``;
                popupRef.current!.style.transform = `translateY(${newDeltaY}px)`; // Appliquer la nouvelle hauteur  la popup

                if (1-newDeltaY/popupRef.current?.clientHeight! <= 0.8){
                    overlayRef.current!.style.backgroundColor = `rgba(0, 0, 0, ${1-newDeltaY/popupRef.current?.clientHeight!})`
                }
                
            setDeltaY(newDeltaY); // Mettre  jour deltaY avec la nouvelle diffrence de position verticale
            }

    }
  };

  const handleTouchEnd = (e: any) => {
    // Si la popup est suffisamment rduite, la fermer
    if (deltaY > heightPopup/1.8) {
      // Par exemple, fermer la popup si la hauteur a t rduite de plus de 50px
      setAnimation("close");
      setShowPopup(false);
      
      if (showValidPopup){
        setShowValidPopup!(false);
      }

      
    }

    // Rinitialiser la hauteur et les tats de suivi du mouvement du doigt
    e.target.style.transform = `translateY(0px)`;
    e.target.style.transition = `transform .2s ease-in`;
    setStartY(0);
    setDeltaY(0);
  };

  const close = (e: any) => {
    if (!popupRef.current?.contains(e.target)) {
      setAnimation("close");

      setTimeout(() => {
        setShowPopup(false);
        if (showValidPopup){
          setShowValidPopup!(false);
        }
        
      }, 300);
    }
  };

  return (
    <main ref={overlayRef} className="overlay" onClick={close}>
      <div
        ref={popupRef}
        id="popupContainer"
        className={"popup " + animation}
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
      >
        <div className="pill"></div>
        {title !== "" && (<h1 className="popup__title">{title}</h1>)}
        {children}
      </div>
    </main>
  );
};

export default Popup;
