import React, { FunctionComponent, useState } from "react";
import "./login.scss";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const Login: FunctionComponent = () => {
  const navigate = useNavigate();

  const [inputId, setInputId] = useState("");
  const [inputPassword, setInputPassword] = useState("");
  const [submitButtonClass, setSubmitButtonClass] = useState("defaultStateButton");
  const [stateButton, setStateButton] = useState(true);
  const [error, setError] = useState([false,""]);

  const handleInputIdChange = (e:any) => {
    const value = e.target.value;
    setInputId(value);
    updateSubmitButtonColor(value, inputPassword);
  };

  const handleInputPasswordChange = (e:any) => {
    const value = e.target.value;
    setInputPassword(value);
    updateSubmitButtonColor(inputId, value);
  };

  const updateSubmitButtonColor = (value1:string, value2:string) => {
    if (value1 && value2) {
        setSubmitButtonClass("validStateButton");
        setStateButton(false);
    } else {
        setSubmitButtonClass("defaultStateButton");
        setStateButton(true);
    }
  };

  const handleSubmitButton = async (e:any) => {
    e.preventDefault();
    if (inputId && inputPassword) {

        try {
          const response = await axios.post('http://192.168.0.107/pwa/api/actions/inscription.php', {
            id: inputId,
            password: inputPassword
          });

          if (response.data.status === 'error'){
            setError([true,response.data.message])
            setTimeout(() => {
                setError([false,""]);
              }, 1000);
          } else if (response.data.status === 'valid'){
            localStorage.setItem('id', response.data.message.idStudent);
            localStorage.setItem('studentId', response.data.message.studentId);
            localStorage.setItem('firstName', response.data.message.studentFirstName);
            localStorage.setItem('lastName', response.data.message.studentLastName);
            localStorage.setItem('profilePicturePath', response.data.message.profilePicturePath);
            
            
            navigate('/home',{replace: true});
          }
          
        } catch (error) {
          console.log(error);
        }
    }
  };

  const handleLoad = (event:any) => {
    // if (!window.matchMedia('(display-mode: standalone)').matches){
      navigate('/welcome', {replace : true});
    // }
  }

  // useEffect(() => {
  //   const handleWindowChange = () => {
  //     if (!window.matchMedia('(display-mode: standalone)').matches && window.location.href !== "/") {
  //       console.log(window.location.href);
  //       window.location.href = "/";
  //     }
  //   };

  //   handleWindowChange()
  // }, []);

  return (
    
    <div className="contentPage" onLoad={handleLoad}>
      <header className="header">
        <i
          className="fa fa-chevron-left"
          onClick={(e) => {
            e.preventDefault();
            navigate("/welcome", { replace: true });
          }}
        ></i>
      </header>
      <main className="formCard">
        <header className="formCardTexts">
          <h2 className="formCardTexts__title">Accdes  ton espace</h2>
          <h3 className="formCardTexts__subtitle">Ravi de te revoir !</h3>
        </header>

        <form action="submit" className="form">
          <label htmlFor="identifiant" className="form__label">
            Identifiant
            <input onChange={handleInputIdChange}
              type="text"
              name="identifiant"
              id="identifiant"
              placeholder="Identifiant ou e-mail"
            />
          </label>
          <label htmlFor="password" className="form__label passwordLabel">
            Mot de passe
            <input onChange={handleInputPasswordChange}
              type="password"
              name="password"
              id="password"
              placeholder="***********"
            />
            <span>Mot de passe oubli ?</span>
          </label>
          <label htmlFor="stayLogged" className="form__label checkboxLabel">
            <input
              type="checkbox"
              title="stayLogged"
              name="stayLogged"
              id="checkbox"
              placeholder=""
            />
            Rest(e) connect(e)
          </label>
          <button disabled={stateButton}
            className={submitButtonClass}

            onClick={handleSubmitButton}
          >
            Connexion
          </button>
        </form>
        <p className="noAccountText">
          Tu n'as pas de compte ?
          <a
            href="signin"
            onClick={(e) => {
              e.preventDefault();
              // navigate("/signup", { replace: true });
              navigate('/signup', { state: { prevPage: '/login' },replace: true });
            }}
          > Rejoins nous !
          </a>
        </p>

        {error[0] && (
          <p className="popup">{error[1]}</p>
        )}
      </main>

      
      
    </div>
  );
};

export default Login;
