import React, {FunctionComponent,useEffect,useState,} from "react";
import "./NotesView.scss";
import Navbar from "../../components/Navbar";
import {useData} from "../../components/DataContext";
import CardUe from "../../components/CardUe";
import Popup from "../../components/Popup";
import Select from "../../components/Select";
import axios from "axios";

type Test = {
  idTest: number;
  testName: string;
  subjectName: string;
};

const NotesView: FunctionComponent = () => {
  const [showPopupNotifications, setShowPopupNotifications] = useState(false);
  const [showPopupGrade, setShowPopupGrade] = useState(false);
  const [popupContent, setPopupContent] = useState("default");
  const [gradeAddValue,setGradeAddValue] = useState("");
  
  const [testsToAddOfCurrentSubject,setTestsToAddOfCurrentSubject] = useState<string[]>([])
  const moyenne = 12.5;

  const { testsToAdd, subjectsOfTestsToAdd, ueListGrade, loadGrade, loadNewTests, loadUe } = useData()!;
  const [currentGrade,setCurrentGrade] = useState<Test>(testsToAdd[0]);
  useEffect(() => {
    
    loadGrade();
   
  // eslint-disable-next-line react-hooks/exhaustive-deps
  },[])

  useEffect(()=>{
    const filteredTests = testsToAdd.filter((test) => test.subjectName === currentGrade.subjectName);
    const testsArray:string[] = [];
    new Set<string>(filteredTests.map((test:Test) => test.testName)).forEach((testName:string)=>{
      testsArray.push(testName);
    })
    setTestsToAddOfCurrentSubject(testsArray);
  },[currentGrade.subjectName, testsToAdd])

  const handleChangeSubject = (e:any) => {
    const value = e.target.value;
    
    const filteredTests = testsToAdd.filter((test) => test.subjectName === value);
    const testsArray:string[] = [];
    new Set<string>(filteredTests.map((test:Test) => test.testName)).forEach((testName:string)=>{
      testsArray.push(testName);
    })
    setTestsToAddOfCurrentSubject(testsArray);
    const filtered= testsToAdd.filter((test) => test.testName === testsArray[0]);
    setCurrentGrade(filtered[0]);
    
  }
  

  const handleChangeTest = (e:any) => {
    const value = e.target.value;
    const filteredTest = testsToAdd.filter((test) => test.testName === value);
    setCurrentGrade(filteredTest[0]);
    
  }

  

  const handleGrade = (e:any) => {
    const value = e.target.value;
    setGradeAddValue(value);
  }

  const submitGrade = async(e:any) => {
    const formData = new FormData();
    formData.append("test", currentGrade.idTest.toString());
    formData.append("grade", gradeAddValue);
    formData.append("user", localStorage.getItem('id')!);
    const response = await axios.post(
      "http://192.168.0.107/pwa/api/routes/api.php?action=addGrade",
      formData,
      {
        headers: {
          "content-type": "multipart/form-data",
        },
      }
    );

    if (response.data){
      loadNewTests();
      loadUe();
      loadGrade();
      setPopupContent('confirmation');
    }
    
  }

  return (
    <div>
      <main className="contentNotesPage">
        <header className="contentNotesPage__header">
          <h1>Mes notes</h1>
          {testsToAdd.length !== 0 ? (
            <svg
              className="notificationsButton notificationsButton-active"
              width="24"
              height="25"
              viewBox="0 0 24 25"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
              onClick={() => setShowPopupNotifications(true)}
            >
              <path
                d="M10.2998 21.5C10.4672 21.8044 10.7133 22.0583 11.0123 22.2352C11.3113 22.412 11.6524 22.5053 11.9998 22.5053C12.3472 22.5053 12.6883 22.412 12.9873 22.2352C13.2864 22.0583 13.5324 21.8044 13.6998 21.5M6 8.5C6 6.9087 6.63214 5.38258 7.75736 4.25736C8.88258 3.13214 10.4087 2.5 12 2.5C13.5913 2.5 15.1174 3.13214 16.2426 4.25736C17.3679 5.38258 18 6.9087 18 8.5C18 15.5 21 17.5 21 17.5H3C3 17.5 6 15.5 6 8.5Z"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
              <circle cx="16.5" cy="6.90466" r="4.8" stroke="currentColor" />
            </svg>
          ) : (
            <svg
              className="notificationsButton"
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              onClick={() => setShowPopupNotifications(true)}
            >
              <path
                d="M10.2998 21C10.4672 21.3044 10.7133 21.5583 11.0123 21.7352C11.3113 21.912 11.6524 22.0053 11.9998 22.0053C12.3472 22.0053 12.6883 21.912 12.9873 21.7352C13.2864 21.5583 13.5324 21.3044 13.6998 21M6 8C6 6.4087 6.63214 4.88258 7.75736 3.75736C8.88258 2.63214 10.4087 2 12 2C13.5913 2 15.1174 2.63214 16.2426 3.75736C17.3679 4.88258 18 6.4087 18 8C18 15 21 17 21 17H3C3 17 6 15 6 8Z"
                stroke="currentColor"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>
          )}
        </header>
        <article className="content">
          <section className="actionButtons">
            <div className="actionButtons__item">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 25 24"
                fill="none"
              >
                <path
                  d="M8.25 6H16.25M16.25 14V18M16.25 10H16.26M12.25 10H12.26M8.25 10H8.26M12.25 14H12.26M8.25 14H8.26M12.25 18H12.26M8.25 18H8.26M6.25 2H18.25C19.3546 2 20.25 2.89543 20.25 4V20C20.25 21.1046 19.3546 22 18.25 22H6.25C5.14543 22 4.25 21.1046 4.25 20V4C4.25 2.89543 5.14543 2 6.25 2Z"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
              </svg>
              <p>Activer le mode simulation</p>
            </div>

            <div className="actionButtons__item" onClick={()=>{setShowPopupGrade(true);setCurrentGrade(testsToAdd[0])}}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 25 24"
                fill="none"
              >
                <path
                  d="M8.75 12H16.75M12.75 8V16M22.75 12C22.75 17.5228 18.2728 22 12.75 22C7.22715 22 2.75 17.5228 2.75 12C2.75 6.47715 7.22715 2 12.75 2C18.2728 2 22.75 6.47715 22.75 12Z"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
              </svg>
              <p>Ajouter une nouvelle note</p>
            </div>
          </section>

          <section className="semesterAverage">
            {moyenne >= 10 ? (
              <p>
                Tu as actuellement une{" "}
                <strong>moyenne de {moyenne}/20, Bravo ! </strong>
              </p>
            ) : (
              <p>
                Tu as actuellement une{" "}
                <strong>moyenne de {moyenne}/20, Keep working !</strong>
              </p>
            )}
          </section>

          <section className="ueList">
              {ueListGrade.map((ue,index) => (
                <CardUe key={index} ue={ue} loadData={loadGrade}/>
              ))}
          </section>

          
        </article>
      </main>
      <Navbar
        classFirstElement=""
        classSecondElement="active"
        classThirdElement=""
      />

      {showPopupNotifications && (
        <Popup
          showPopup={showPopupNotifications}
          setShowPopup={setShowPopupNotifications}
          title="Notifications"
        >
          <main className="contentPopup notificationsGrid">
            {testsToAdd.length === 0 ? (
              <p className="emptyLabel">Aucune nouvelle notes de disponible.</p>
            ) : (
              testsToAdd!.map((grade, index) => (
                <div key={index} className="notificationsGrid__item">
                  <h4>{grade.subjectName}</h4>
                  <p>
                    Ta note pour {grade.testName} est disponible sur Moodle.
                  </p>
                </div>
              ))
            )}
          </main>
        </Popup>
      )}

      {showPopupGrade && (
        <Popup
          showPopup={showPopupGrade}
          setShowPopup={setShowPopupGrade}
          
          title="Ajouter une note"
        >
           {popupContent === "default" ? 
           
           (<main className="contentPopup addGradeForm">
          <Select
              id="subject"
              label="Slectionne la matire"
              value={""}
              name="subject"
              onChange={handleChangeSubject}
              options={subjectsOfTestsToAdd}
            />
            <Select
              id="test"
              label="Slectionne le contrle"
              value={"Slectionne le contrle"}
              name="test"
              onChange={handleChangeTest}
              options={testsToAddOfCurrentSubject}
              
            />
            <section className="grade" >
              <input className="gradeInput" onChange={handleGrade} type="text" name="grade" id="grade" max={20}  inputMode="decimal" placeholder="12.7"/>
            </section>
            {Number(gradeAddValue.slice(0,2)) <= 20 && Number(gradeAddValue.slice(0,2)) >= 0 && gradeAddValue.length <= 6 && (
            
            <button className="cta" onClick={submitGrade}>Ajouter</button>
            
            )}
          </main>):(
          
          <main className="contentPopup gradeAdded">
              <div className="gradeAdded__icon">
                <p></p>
              </div>
              <h1 className="gradeAdded__message">Ta note a t ajout avec succs !</h1>
          </main>)
          
          }
        </Popup>
      )}


    </div>
  );
};

export default NotesView;
