import React, { FunctionComponent } from "react";
import "./onboarding.scss";
import { useNavigate } from "react-router-dom";

const Onboarding: FunctionComponent = () => {
  const navigate = useNavigate();

  

  return (
    <div className="wrapper onboarding">
      <header className="onboarding__texts">
        <h2>ESIRESULTATS</h2>
        <h1>Ajoute tes notes en quelques clics.</h1>
      </header>

      <section className="onboarding__popup popupOnboarding">
        <header className="popupOnboarding__header">
          <div className="pill"></div>
          <h2>Notifications</h2>
        </header>
        <img src="/onboarding-3.png" alt="" />
        <footer className="popupOnboarding__footer">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            viewBox="0 0 16 16"
            fill="none"
          >
            <path
              d="M16 7H3.83L9.42 1.41L8 0L0 8L8 16L9.41 14.59L3.83 9H16V7Z"
              fill="#F4F8F6"
            />
          </svg>
          <div className="nextButton">
            <p
              onClick={(e) => {
                e.preventDefault();
                navigate("/welcome", { replace: true });
              }}
            >
              Commencer
            </p>
          </div>
        </footer>
      </section>
    </div>
  );
};

export default Onboarding;
