import React, { FunctionComponent, useEffect, useState } from "react";
import "./instructions.scss";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const Instructions: FunctionComponent = () => {

  // const [text,setText] = useState('Hello');
  const [ret,setRet] = useState('loading');

  useEffect(()=>{
    const stock = async()=>{
    
      try {
        const response = await axios.post(
          "http://192.168.0.107/pwa/api/actions/crypt.php?action=e",
          {
            text: ret,
          }
        );
        localStorage.setItem('id',response.data);
      } catch (error) {
        console.log(error);
      }
    }

    stock();

  },[]);

  const decrypt = async()=>{
    
    try {
      const response = await axios.post(
        "http://192.168.0.107/pwa/api/actions/crypt.php?action=d",
        {
          text: localStorage.getItem('id'),
        }
      );
      localStorage.removeItem('id')
    } catch (error) {
      console.log(error);
    }
  }
  

  const handleChange = (e:any) =>{
    setRet(e.target.value);
  }
  return (
    <div>
        <h1>{localStorage.getItem('id')}</h1>
        <button onClick={decrypt}>Decrypt</button>
    </div>
  );
};

export default Instructions;
