import React, { Suspense, createContext, useContext, useState } from "react";
import Welcome from "./pages/welcome_page/Welcome";
import Onbarding from "./pages/onboarding_page/Onboarding";
import Login from "./pages/login_page/Login";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Instructions from "./pages/instructions_page/Instructions";
// import Signup from "./pages/signup_page/Signup";
import Home from "./pages/home_page/Home";
import NotesView from "./pages/notes_page/NotesView";
import { DataProvider } from "./components/DataContext";
import ProfilView from "./pages/profil_page/ProfilView";
import { requestForToken } from "./firebase";



type Test = {
  idTest: number;
  testName: string;
  subjectName: string;
};

const TestsContext = createContext<{ testsToAdd: Test[]; setTestsToAdd: React.Dispatch<React.SetStateAction<Test[]>> }>({
  testsToAdd: [],
  setTestsToAdd: () => {},
});


const App: React.FC = () => {
  const [testsToAdd, setTestsToAdd] = useState<Test[]>([]);

  requestForToken();
 
  return (
    
    <DataProvider>
      
    <Router>
      <Suspense fallback={<div>Loading...</div>}>
      <TestsContext.Provider value={{ testsToAdd, setTestsToAdd }}>
        <Routes>
          <Route path="/" element={<Instructions />} />
          <Route path="/onboarding" element={<Onbarding />} />
          <Route path="/welcome" element={<Welcome />} />
          {/* <Route path="/signup" element={<Signup />} /> */}
          <Route path="/login" element={<Login />} />
          <Route path="/home" element={<Home />} />
          <Route path="/notes" element={<NotesView />} />
          <Route path="/profil" element={<ProfilView />} />
          <Route
            path="/install-instructions"
            element={<Instructions />}
          />
        </Routes>
        </TestsContext.Provider>
      </Suspense>
    </Router>
    </DataProvider>
  );
};

const useTestsContext = () => useContext(TestsContext);
  

export {App, TestsContext, useTestsContext};
