import React, { ChangeEvent, FunctionComponent, RefObject } from "react";
import "./Select.scss";
interface InputProps {
  id: string;
  value: string;
  label: string;
  onChange: (event: ChangeEvent<HTMLSelectElement>) => void;
  type?: string;
  name: string;
  options: string[];
  selectedRef?: any;
}

const Select: FunctionComponent<InputProps> = ({
  id,
  value,
  label,
  onChange,
  name,
  options,
  selectedRef
}) => {
  return (
    <div className="input">
      <select
        spellCheck={false}
        id={id}
        name={name}
        onChange={onChange}
        title={name}
        ref={selectedRef}
      >
        <option value="default" disabled hidden>{label}</option>
        {options.map((specialty: any, index: number) => (
          specialty === value ? ( <option key={index} value={specialty} selected>
            {specialty}
          </option>) : ( <option key={index} value={specialty}>
            {specialty}
          </option>)
         
        ))}
      </select>
    </div>
  );
};

export default Select;
