import React, {
  FunctionComponent,
  useEffect,
  useState,
} from "react";
import "./CardUe.scss";
import Popup from "./Popup";
import axios from "axios";
import { useData } from "./DataContext";
interface UeProps {
  ue: Ue;
  loadData: () => void;
}


type Grade = {
  idGrade: number;
  gradeValue: number;
  idTest: number;
  idStudent: number;
  testName: string;
  testCoefficient: number;
  testPlateform: string;
  idSubject: number;
  subjectName: string;
  subjectCoefficient: number;
};
type Subject = {
  average: number;
  grades: Grade[];
  idSubject: number;
  idUe: number;
  subjectCoefficient: number;
  subjectName: string;
};

type Ue = {
  average: number;
  idSemester: number;
  idSpecialty: number;
  idUe: number;
  subjects: Subject[];
  ueImagePath: string;
  ueName: string;
  ueNumber: string;
};



const CardUe: FunctionComponent<UeProps> = ({ ue, loadData }) => {
  const [showContentCard, setShowContentCard] = useState(
    ue.average === 0 ? false : true
  );
  const [showGradePopup, setShowGradePopup] = useState(false);
  const [activeGrade, setActiveGrade] = useState<Grade | null>();
  const [popupContent, setPopupContent] = useState<String>("default");
  const [displayEditButton, setDisplayEditButton] = useState(false);
  const {loadNewTests, loadUe } = useData()!;
  const [newGradeValue, setNewGradeValue] = useState<Number>(
    activeGrade?.gradeValue!
  );

  const handleClickGrade = (grade: Grade) => {
    setShowGradePopup(true);
    setActiveGrade(grade);
  };

  useEffect(() => {
    setPopupContent("default");
  }, [showGradePopup]);

  const handleChangeNewGrade = (e: any) => {
    const value = e.target.value;
    setNewGradeValue(Number(value));

    Number(value) === activeGrade?.gradeValue ||
    Number(value) > 20 ||
    Number(value) < 0 ||
    String(value).length > 6
      ? setDisplayEditButton(false)
      : setDisplayEditButton(true);
  };

  const editGrade = async(e:any) => {
    const formData = new FormData();
    formData.append("id", activeGrade?.idGrade.toString()!);
    formData.append("grade", newGradeValue.toString());
    const response = await axios.post(
      "http://192.168.0.107/pwa/api/routes/api.php?action=editGrade",
      formData,
      {
        headers: {
          "content-type": "multipart/form-data",
        },
      }
    );

    if (response.data){
        loadData();
        loadNewTests();
        loadUe();
        setPopupContent('editConfirmation');
    }
  }

  const deleteGrade = async(e:any) => {
    const formData = new FormData();
    formData.append("id", activeGrade?.idGrade.toString()!);
    const response = await axios.post(
      "http://192.168.0.107/pwa/api/routes/api.php?action=deleteGrade",
      formData,
      {
        headers: {
          "content-type": "multipart/form-data",
        },
      }
    );

    if (response.data){
        loadData();
        loadNewTests();
        loadUe();
        setPopupContent('deleteConfirmation');
    }
  }

  return (
    <div
      className={"ueComponent " + (showContentCard ? "open" : "close")}
      key={ue.idUe}
    >
      <header
        className={
          "ueComponent__header headerUeComponent " +
          (showContentCard ? "open" : "close")
        }
        onClick={() => {
          setShowContentCard(!showContentCard);
        }}
      >
        <p className="headerUeComponent__ueName">
          UE {ue.ueNumber} : {ue.ueName}
        </p>
        <div className="headerUeComponent__details">
          <p className="headerUeComponent__ueAverage">
            {ue.average === 0 ? "..,.." : ue.average}/20
          </p>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="18"
            height="18"
            viewBox="0 0 18 18"
            fill="none"
          >
            <g clip-path="url(#clip0_210_1087)">
              <path
                d="M12 7.5L9 10.5L6 7.5M16.5 9C16.5 13.1421 13.1421 16.5 9 16.5C4.85786 16.5 1.5 13.1421 1.5 9C1.5 4.85786 4.85786 1.5 9 1.5C13.1421 1.5 16.5 4.85786 16.5 9Z"
                stroke="#F4F8F6"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </g>
            <defs>
              <clipPath id="clip0_210_1087">
                <rect width="18" height="18" fill="white" />
              </clipPath>
            </defs>
          </svg>
        </div>
      </header>
      <main
        className={"contentWrapper " + (showContentCard ? "open" : "close")}
      >
        <main className={"ueComponent__content subjectGrid"}>
          {ue.subjects.map((subject) => (
            <section className="itemSubject" key={subject.idSubject}>
              <header className="itemSubject__header">
                <p className="subjectName">{subject.subjectName}</p>
                <div className="detailsSubject">
                  <p className="coefficient">
                    Coeff. {subject.subjectCoefficient}
                  </p>
                  <p className="subjectAverage">
                    {subject.average === 0 ? "..,.." : subject.average}/20
                  </p>
                </div>
              </header>
              <main
                className={
                  "gradesGrid " + (subject.grades.length === 0 ? "empty" : "")
                }
              >
                {subject.grades.map((grade) => (
                  <div
                    className="gradesGrid__item"
                    key={grade.idGrade}
                    onClick={() => handleClickGrade(grade)}
                  >
                    <p className="grade">{grade.gradeValue}/20</p>
                    <div className="coefficientGrade">
                      <p>{grade.testCoefficient}</p>
                    </div>
                  </div>
                ))}
              </main>
            </section>
          ))}
        </main>
      </main>

      {showGradePopup && (
        <Popup
          showPopup={showGradePopup}
          setShowPopup={setShowGradePopup}
          title=""
        >
          {popupContent !== "deleteConfirmation" &&
            popupContent !== "editConfirmation" && (
              <main className="contentPopup gradeDetailsPopup">
                <header className="gradeDetailsPopup__header detailsGrade">
                  <h1 className="detailsGrade__testName">
                    {activeGrade?.testName}
                  </h1>
                  <p className="detailsGrade__subjectName">
                    {activeGrade?.subjectName}
                  </p>
                  <p className="detailsGrade__testCoefficient">
                    Coeff. {activeGrade?.testCoefficient}
                  </p>
                </header>

                <div className="gradeDetailsPopup__content gradeValue">
                  {popupContent === "edit" ? (
                    <input
                      className="gradeInput"
                      onChange={handleChangeNewGrade}
                      type="text"
                      name="grade"
                      id="grade"
                      max={20}
                      inputMode="decimal"
                      placeholder={activeGrade?.gradeValue.toString()}
                    />
                  ) : (
                    <h1>{activeGrade?.gradeValue}/20</h1>
                  )}
                </div>
                <footer className="gradeDetailsPopup__footer actionsGrade">
                  {popupContent === "default" ? (
                    <div
                      className="actionsGrade__item"
                      onClick={() => setPopupContent("delete")}
                    >
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 16 17"
                        fill="none"
                      >
                        <g clip-path="url(#clip0_210_1225)">
                          <path
                            d="M2 4.49998H14M12.6667 4.49998V13.8333C12.6667 14.5 12 15.1666 11.3333 15.1666H4.66667C4 15.1666 3.33333 14.5 3.33333 13.8333V4.49998M5.33333 4.49998V3.16665C5.33333 2.49998 6 1.83331 6.66667 1.83331H9.33333C10 1.83331 10.6667 2.49998 10.6667 3.16665V4.49998"
                            stroke="currentColor"
                            stroke-opacity="0.8"
                            stroke-width="1.8"
                            stroke-linecap="round"
                            stroke-linejoin="round"
                          />
                        </g>
                        <defs>
                          <clipPath id="clip0_210_1225">
                            <rect
                              width="16"
                              height="16"
                              fill=""
                              transform="translate(0 0.5)"
                            />
                          </clipPath>
                        </defs>
                      </svg>
                      <p>Supprimer</p>
                    </div>
                  ) : (
                    <div
                      className="actionsGrade__item"
                      onClick={() => setPopupContent("default")}
                    >
                      <p>Annuler</p>
                    </div>
                  )}

                  {popupContent === "edit" && displayEditButton && (
                    <div className="actionsGrade__item editGrade" onClick={editGrade}>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 16 17"
                        fill="none"
                      >
                        <g clip-path="url(#clip0_210_1264)">
                          <path
                            d="M10.0002 3.83335L12.6668 6.50002M11.3335 2.50004C11.5033 2.29939 11.7133 2.13608 11.9502 2.02049C12.187 1.90491 12.4456 1.83959 12.7093 1.82871C12.973 1.81783 13.236 1.86162 13.4818 1.9573C13.7275 2.05297 13.9504 2.19842 14.1364 2.38438C14.3223 2.57035 14.4672 2.79275 14.5619 3.0374C14.6565 3.28205 14.6987 3.54359 14.6859 3.80536C14.6731 4.06712 14.6055 4.32336 14.4874 4.55775C14.3694 4.79214 14.2034 4.99953 14.0002 5.1667L5.00016 14.1667L1.3335 15.1667L2.3335 11.5L11.3335 2.50004Z"
                            stroke="currentColor"
                            stroke-opacity="0.8"
                            stroke-width="1.8"
                            stroke-linecap="round"
                            stroke-linejoin="round"
                          />
                        </g>
                        <defs>
                          <clipPath id="clip0_210_1264">
                            <rect
                              width="16"
                              height="16"
                              fill=""
                              transform="translate(0 0.5)"
                            />
                          </clipPath>
                        </defs>
                      </svg>
                      <p>Modifier</p>
                    </div>
                  )}

                  {popupContent === "delete" && (
                    <div className="actionsGrade__item deleteGrade" onClick={deleteGrade}>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 16 17"
                        fill="none"
                      >
                        <g clip-path="url(#clip0_210_1225)">
                          <path
                            d="M2 4.49998H14M12.6667 4.49998V13.8333C12.6667 14.5 12 15.1666 11.3333 15.1666H4.66667C4 15.1666 3.33333 14.5 3.33333 13.8333V4.49998M5.33333 4.49998V3.16665C5.33333 2.49998 6 1.83331 6.66667 1.83331H9.33333C10 1.83331 10.6667 2.49998 10.6667 3.16665V4.49998"
                            stroke="currentColor"
                            stroke-opacity="0.8"
                            stroke-width="1.8"
                            stroke-linecap="round"
                            stroke-linejoin="round"
                          />
                        </g>
                        <defs>
                          <clipPath id="clip0_210_1225">
                            <rect
                              width="16"
                              height="16"
                              fill=""
                              transform="translate(0 0.5)"
                            />
                          </clipPath>
                        </defs>
                      </svg>
                      <p>Supprimer</p>
                    </div>
                  )}

                  {popupContent === "default" && (
                    <div
                      className="actionsGrade__item"
                      onClick={() => {
                        setPopupContent("edit");
                      }}
                    >
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 16 17"
                        fill="none"
                      >
                        <g clip-path="url(#clip0_210_1264)">
                          <path
                            d="M10.0002 3.83335L12.6668 6.50002M11.3335 2.50004C11.5033 2.29939 11.7133 2.13608 11.9502 2.02049C12.187 1.90491 12.4456 1.83959 12.7093 1.82871C12.973 1.81783 13.236 1.86162 13.4818 1.9573C13.7275 2.05297 13.9504 2.19842 14.1364 2.38438C14.3223 2.57035 14.4672 2.79275 14.5619 3.0374C14.6565 3.28205 14.6987 3.54359 14.6859 3.80536C14.6731 4.06712 14.6055 4.32336 14.4874 4.55775C14.3694 4.79214 14.2034 4.99953 14.0002 5.1667L5.00016 14.1667L1.3335 15.1667L2.3335 11.5L11.3335 2.50004Z"
                            stroke="currentColor"
                            stroke-opacity="0.8"
                            stroke-width="1.8"
                            stroke-linecap="round"
                            stroke-linejoin="round"
                          />
                        </g>
                        <defs>
                          <clipPath id="clip0_210_1264">
                            <rect
                              width="16"
                              height="16"
                              fill=""
                              transform="translate(0 0.5)"
                            />
                          </clipPath>
                        </defs>
                      </svg>
                      <p>Modifier</p>
                    </div>
                  )}
                </footer>
              </main>
            )}

          {popupContent === "editConfirmation" && (
            <main className="contentPopup gradeAdded">
              <div className="gradeAdded__icon">
                <p></p>
              </div>
              <h1 className="gradeAdded__message">
                Ta note a t modifi avec succs !
              </h1>
            </main>
          )}

          {popupContent === "deleteConfirmation" && (
            <main className="contentPopup gradeAdded">
              <div className="gradeAdded__icon">
                <p></p>
              </div>
              <h1 className="gradeAdded__message">
                Ta note a t supprim avec succs !
              </h1>
            </main>
          )}
        </Popup>
      )}
    </div>
  );
};

export default CardUe;
