import React, { ChangeEvent, FunctionComponent } from 'react';
import './LittleCardUe.scss';
interface UeProps {
    ueNumber:number,
    ueName: string;
    ueAverage: number;
    ueBackgroundImage?: string;
}

const LittleCardUe: FunctionComponent<UeProps> = ({ueNumber,ueName,ueAverage,ueBackgroundImage}) => {
  return (
        <div className='ueCard' >
            <img src="http://192.168.0.107/pwa/src/data/uePictures/card.png" className='ueCard__background' alt="" />
            <section className="ueCard__content contentCard">
                <div className="contentCard__pill">
                    <p>UE {ueNumber}</p>
                </div>

                <div className="contentCard__texts">
                    <p className="title">{ueName}</p>
                    <p className={"grade "+(ueAverage === 0 ? "null": "")}>{ueAverage === 0 ? "..,.." : ueAverage}/20</p>
                </div>
            </section>
            {/* <h2 className='icon'></h2>
            <section className="ueCard__details">
                
            </section> */}
      </div>
  );
};

export default LittleCardUe;
