import React, { ChangeEvent, FunctionComponent } from 'react';
import './Input.scss';
interface InputProps {
    id:string,
    value: string;
    onChange?: (event: ChangeEvent<HTMLInputElement>) => void;
    type?: string;
    name:string;
    placeholder?: string;
    disabled?:boolean
}

const Input: FunctionComponent<InputProps> = ({ id, value, onChange, type, name, placeholder,disabled=false }) => {
    
    const setTop = () => {
        // document.querySelector('.contentPage')!.scrollTop = 0;
        window.innerHeight = 944

    }
    return (
        <div className='input'>
            <input
                    spellCheck={false}
                    id={id}
                    type={type}
                    value={value}
                    onChange={onChange}
                    placeholder={placeholder}
                    readOnly={disabled}
                    onFocus={setTop}
                    autoCapitalize='off'
                    
                />
      </div>
  );
};

export default Input;
