// DataContext.js
import axios from "axios";
import React, {
  FunctionComponent,
  ReactNode,
  createContext,
  useContext,
  useState,
} from "react";



interface DataProps {
  children: ReactNode;
}


type Test = {
  idTest: number;
  testName: string;
  subjectName: string;
};

type Ue = {
  average: number;
  idUe: number;
  ueName: string;
  ueNumber: number;
  ueImagePath: string;
};

type UeDetail = {
  average: number;
  idSemester: number,
  idSpecialty: number,
  idUe: number,
  subjects:Subject[],
  ueImagePath: string,
  ueName:string,
  ueNumber:string
}

type Grade = {
  idGrade:number,
  gradeValue:number,
  idTest:number,
  idStudent:number,
  testName:string,
  testCoefficient:number,
  testPlateform:string,
  idSubject:number,
  subjectName:string,
  subjectCoefficient:number
}
type Subject = {
average: number;
grades:Grade[],
idSubject: number,
idUe: number,
subjectCoefficient: number,
subjectName: string;
}

interface DataContextType {
  testsToAdd: Test[];
  subjectsOfTestsToAdd:string[];
  ueList: Ue[];
  ueListGrade:UeDetail[];
  loadNewTests: () => void; // Le type de votre fonction
  loadUe:()=> void;
  loadGrade:()=>void;
}

const DataContext = createContext<DataContextType | undefined>(undefined);


export const DataProvider: FunctionComponent<DataProps> = ({ children }) => {
  const [testsToAdd, setTestsToAdd] = useState<Test[]>([]);
  const [ueList, setUeList] = useState<Ue[]>([]);
  const [subjectsOfTestsToAdd, setSubjectsOfTestsToAdd] = useState<string[]>(
    []
  );
  const [ueListGrade,setUeListGrade] = useState<UeDetail[]>([]);

  const loadNewTests = async () => {
    const testsListFromLocal = localStorage.getItem("testsToAdd")!;

    if (testsListFromLocal) {
      setTestsToAdd(JSON.parse(testsListFromLocal));
      let subjectsArray: string[] = [];
      new Set<string>(
        JSON.parse(testsListFromLocal).map((test: Test) => test.subjectName)
      ).forEach((subjectName: string) => {
        subjectsArray.push(subjectName);
      });

      setSubjectsOfTestsToAdd(subjectsArray);

      const formData = new FormData();
      formData.append("user", localStorage.getItem("id")!);
      const response = await axios.post(
        "http://192.168.0.107/pwa/api/routes/api.php?action=getNewTests",
        formData,
        {
          headers: {
            "content-type": "multipart/form-data",
          },
        }
      );
      setTestsToAdd(response.data);
      localStorage.setItem("testsToAdd", JSON.stringify(response.data));
      subjectsArray = [];
      new Set<string>(
        response.data.map((test: Test) => test.subjectName)
      ).forEach((subjectName: string) => {
        subjectsArray.push(subjectName);
      });

      setSubjectsOfTestsToAdd(subjectsArray);
    } else {
      const formData = new FormData();
      formData.append("user", localStorage.getItem("id")!);
      const response = await axios.post(
        "http://192.168.0.107/pwa/api/routes/api.php?action=getNewTests",
        formData,
        {
          headers: {
            "content-type": "multipart/form-data",
          },
        }
      );
      setTestsToAdd(response.data);
      localStorage.setItem("testsToAdd", JSON.stringify(response.data));
      const subjectsArray: string[] = [];
      new Set<string>(
        response.data.map((test: Test) => test.subjectName)
      ).forEach((subjectName: string) => {
        subjectsArray.push(subjectName);
      });

      setSubjectsOfTestsToAdd(subjectsArray);
    }
  };

  const loadUe = async () => {
    const ueListFromLocal = localStorage.getItem("ueList")!;

    if (ueListFromLocal) {
      setUeList(JSON.parse(ueListFromLocal));
    }

    const formData = new FormData();
    formData.append("user", localStorage.getItem("id")!);
    const response = await axios.post(
      "http://192.168.0.107/pwa/api/routes/api.php?action=getAllUe&simulation=false",
      formData,
      {
        headers: {
          "content-type": "multipart/form-data",
        },
      }
    );
    setUeList(response.data);
    localStorage.setItem("ueList", JSON.stringify(response.data));
  };

  const loadGrade = async() => {
    
    const ueListGradeFromStorage = localStorage.getItem('ueListGrade')!;
    if (ueListGradeFromStorage){
      setUeListGrade(JSON.parse(ueListGradeFromStorage));
    }
      const formData = new FormData();
      formData.append("user", localStorage.getItem('id')!);
      const response = await axios.post(
        "http://192.168.0.107/pwa/api/routes/api.php?action=getAllGrades&simulation=false",
        formData,
        {
          headers: {
            "content-type": "multipart/form-data",
          },
        }
      );

      localStorage.setItem('ueListGrade',JSON.stringify(response.data));
      setUeListGrade(response.data);
  }

  return (
    <DataContext.Provider value={{ testsToAdd, subjectsOfTestsToAdd, ueList, ueListGrade, loadNewTests,loadUe, loadGrade }}>
      {children}
    </DataContext.Provider>
  );
};

export const useData = () => useContext(DataContext);
